<template>
  <div>
    <Bar
      id="it-chart"
      :options="chartOptions"
      :data="chartData"
    />
  </div>
</template>

<script>
import { Bar } from 'vue-chartjs'
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js'
import ChartDataLabels from 'chartjs-plugin-datalabels'

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale, ChartDataLabels)

export default {
  name: 'BarChart',
  components: { Bar },
  data() {
    return {
      chartData: {
        labels: ['Section A', 'Section B', 'Section C'],
        datasets: [
          {
            label: 'Boys',
            data: [10, 15, 20],
            backgroundColor: 'blue'
          },
          {
            label: 'Girls',
            data: [12, 18, 14],
            backgroundColor: 'pink'
          }
        ]
      },
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: {
            position: 'top',
          },
          title: {
            display: true,
            text: 'Information Technology'
          },
          datalabels: {
            anchor: 'end',
            align: 'end',
            formatter: (value, context) => {
              const total = context.chart.data.datasets.reduce((acc, dataset) => acc + dataset.data[context.dataIndex], 0);
              const percentage = ((value / total) * 100).toFixed(1); // Calculate percentage
              return `${percentage}%`; // Return percentage string
            },
            color: 'black' // Color of the text
          }
        },
        scales: {
          x: {
            stacked: false
          },
          y: {
            title: {
              display: true,
              text: 'Count',
              font: {
                size: 14
              }
            },
            max: 30 // Adjust based on your data
          }
        }
      }
    }
  }
}
</script>

<style scoped>
#it-chart {
  height: 400px; /* Adjust height as needed */
  width: 600px;  /* Adjust width as needed */
}
</style>
